.SignOut {
  background-color: #008BF8;
  border: 0;
  border-radius: 5px;
  color: white;
  margin: 0 auto;
  padding: 7px;
  -webkit-transition: background 0.2s;
  -o-transition: background 0.2s;
  transition: background 0.2s;
  font-family: 'helvetica neue', sans-serif;
  font-size: 12px;
  font-weight: 200;
  cursor: pointer;
  cursor: hand;
}

.SignOut:hover{
  background-color: #0069bc;
}


