.NoteList {
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  width: 30rem;
}

@media (max-width: 800px) {
  .NoteList {
    width: 20rem;
  }
}

.NoteList h3 {
  color: #999;
  text-transform: uppercase;
  font-size: 2rem;
  font-family: Oxygen;
  font-weight: 300;
  letter-spacing: 3px;
  margin: 20px 2rem;
}

.NoteList button{
  height: 25px;
  width: 50px;
  margin: 0.5rem 3rem;
  border: none;
  border-radius: 3px;
  cursor: hand;
  cursor: pointer;
}

.NoteList button:hover{
  background-color: darkgray;
  -webkit-transition: 0.9s;
  -o-transition: 0.9s;
  transition: 0.9s;
}

.NoteList i{
  margin: auto;
  font-size: 16px;
}

.NoteList li{
}

ul#notes {
  border-top: 1px solid #eee;
  overflow-y: scroll;
  height: calc(100vh - 72px);
  list-style: none;
  margin-top: 1em;
  padding: 0;
  width: 100%;
  color: #999;
}

ul#notes > li {
  font-size: 90%;
  cursor: pointer;
  /*overflow: hidden;*/
}

ul#notes > li,
ul#notes > li, ul#notes > li * {
  -webkit-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  -o-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

ul#notes li .note {
  border-top: 1px solid #eee;
  margin: 0 2rem;
  padding: 1rem 4px; 
}
ul#notes li:first-of-type .note {
  border-top: none; 
}
ul#notes li:hover {
  background-color: #008BF8;
}
ul#notes li:hover,
ul#notes li:hover .note * {
  color: white !important;
  text-decoration: none !important;
}
ul#notes li:hover .note {
  border-color: transparent;
}
ul#notes li .note-title {
  color: #4a4a4a;
  font-family: "Fauna One";
  font-size: 120%;
  font-weight: 400;
  white-space: nowrap;
  overflow-x: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
ul#notes li .note-body {
  height: 35px;
  overflow: hidden;
  margin-top: 0.5rem;
}
ul#notes li .note-body * {
  font-size: 1em !important;
  margin: 0 !important;
  padding: 0 !important;
  color: #999 !important;
  background: none;
  border: none;
}
ul#notes li .note {
  display: block;
  height: 70px;
  padding: 1em;
  text-decoration: none;
}



