.NoteForm {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 0 3rem;
}
.NoteForm .form-actions {
  padding-top: 1rem;
  margin-left: -2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
}
/*.NoteForm button {
  border: none;
  background: none;
  padding: 0;
}*/
.NoteForm button i.fa {
  color: #999;
  font-size: 2rem;
  margin: 0;
}
.NoteForm form {
  margin: 0 auto;
  max-width: 80rem;
}

.NoteForm.hidden{
  display: none;
}

.NoteForm button{
  height: 36px;
  padding: 0 1rem;
  min-width: 50px;
  margin-right: 2rem;
  background: #008BF8;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: hand;
  cursor: pointer;
}

.NoteForm button:hover{
  background-color: #0054b5;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.NoteForm i{
  font-size: 20px;
  color: white;
}

.fa-trash-o:before {
  content: "\f014";
  color: white;
}

input[name="title"] {
  border: none;
  font-size: 200%;
  font-family: "Fauna One";
  color: #008BF8;
  font-weight: 400;
  width: 100%;
}
input[name="title"] {
  outline: none;
}
textarea {
  border-color: #eee;
  width: 100%;
  height: calc(100vh - 140px);
  font-size: 1.3em;
}


